.search__filters {
  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    margin-bottom: unit(2) !important;
  }

  @include respond-to('medium-large') {
    padding: 0 unit(2);
  }
}

.search__filters-heading {
  position: relative;
  text-align: center;

  @include respond-to('medium-large') {
    display: none;
  }

  [class*='heading-'] {
    display: inline-block;
    line-height: unit(1.7);
    margin: 0;
  }
}

.search__filters-reset {
  @include reset-button;
  bottom: 0;
  color: $color--rose-of-sharon;
  font: 1.3rem/2.6 $font-family--sans;
  position: absolute;
  right: 0;
}

.search__archives {
  line-height: 2.5rem;
  margin: 0 0 unit(2);
  overflow: hidden;
  position: relative;

  label {
    display: block;
    font-size: 1.3rem;
    padding-right: 3.8rem;
    text-align: right;

    &::before,
    &::after {
      @include pseudo;
      height: 25px;
      right: 0;
      top: 0;
      width: 25px;
    }

    &::before {
      background-color: $color--cararra;
      border-radius: 2px;
      box-shadow: inset 1px 2px 3px rgba($color--black, 0.2) !important;
      z-index: 1;
    }

    &::after {
      z-index: 2;
    }
  }

  input[type='checkbox'] {
    left: -5rem;
    position: absolute;

    &:checked + label::after {
      background: url(#{$path}/images/checkbox.svg) no-repeat center;
    }
  }
}
